// src/pages/interview.js
import { useState } from "react";
import { questions } from "@/data/questions";
import Header from "@/layouts/header";
import toast, { Toaster } from "react-hot-toast";

export default function InterviewPage() {
    const [step, setStep] = useState(1);
    const [formData, setFormData] = useState({
        fullName: "",
        email: "",
        phone: "",
        linkedin: "",
        answers: {},
        resume: null,
        coverLetter: null,
    });
    const [currentPage, setCurrentPage] = useState(0);
    const [submissionStatus, setSubmissionStatus] = useState(null);
    const [isSubmitting, setIsSubmitting] = useState(false);

    const questionsPerPage = 5;
    const totalPages = Math.ceil(questions.length / questionsPerPage);
    const currentQuestions = questions.slice(
        currentPage * questionsPerPage,
        (currentPage + 1) * questionsPerPage
    );

    const handleNext = async () => {
        if (step === 1) {
            if (!formData.fullName || !formData.email) {
                toast.error("Please fill in your name and email.");
                return;
            }
            setStep(2);
            toast.success("Great! Let’s move to the questions.");
        } else if (step === 2 && currentPage < totalPages - 1) {
            setCurrentPage(currentPage + 1);
            toast("Moving to the next set of questions...", { icon: "➡️" });
        } else if (step === 2) {
            setStep(3);
            toast.success("All questions completed! Upload your documents next.");
        } else if (step === 3) {
            setIsSubmitting(true);
            const formDataToSend = new FormData();
            formDataToSend.append("fullName", formData.fullName);
            formDataToSend.append("email", formData.email);
            formDataToSend.append("phone", formData.phone);
            formDataToSend.append("linkedin", formData.linkedin);
            formDataToSend.append("answers", JSON.stringify(formData.answers));

            if (formData.resume) {
                const resumeBase64 = await fileToBase64(formData.resume);
                formDataToSend.append(
                    "resume",
                    JSON.stringify({ data: resumeBase64, type: formData.resume.type })
                );
            }
            if (formData.coverLetter) {
                const coverLetterBase64 = await fileToBase64(formData.coverLetter);
                formDataToSend.append(
                    "coverLetter",
                    JSON.stringify({
                        data: coverLetterBase64,
                        type: formData.coverLetter.type,
                    })
                );
            }

            console.log("FormData to send:", {
                fullName: formData.fullName,
                email: formData.email,
                phone: formData.phone,
                linkedin: formData.linkedin,
                answers: formData.answers,
                resume: formData.resume ? "present" : "none",
                coverLetter: formData.coverLetter ? "present" : "none",
            });

            const submitToast = toast.loading("Submitting your application...");

            try {
                const response = await fetch("/api/submit", {
                    method: "POST",
                    body: formDataToSend,
                });
                const result = await response.json();
                if (response.ok) {
                    toast.success("Submission successful!", { id: submitToast });
                    setSubmissionStatus({ success: true, score: result.score });
                    setStep(4); // Move to confirmation step
                } else {
                    throw new Error(result.error);
                }
            } catch (error) {
                toast.error("Submission failed. Please try again.", { id: submitToast });
                setSubmissionStatus({ success: false, message: error.message });
            } finally {
                setIsSubmitting(false);
            }
        }
    };

    const handleBack = () => {
        if (step === 2 && currentPage > 0) {
            setCurrentPage(currentPage - 1);
            toast("Going back to the previous set...", { icon: "⬅️" });
        } else if (step > 1) {
            setStep(step - 1);
            toast("Returning to the previous step...", { icon: "⬅️" });
        }
    };

    const handleChange = (e) => {
        const { name, value, files } = e.target;
        if (files) {
            setFormData({ ...formData, [name]: files[0] });
            toast.success(`${name === "resume" ? "Resume" : "Cover Letter"} selected!`);
        } else if (step === 1) {
            setFormData({ ...formData, [name]: value });
        }
    };

    const handleOptionToggle = (questionIndex, option) => {
        const currentAnswers = formData.answers[questionIndex] || [];
        let newAnswers;
        if (currentAnswers.includes(option)) {
            newAnswers = currentAnswers.filter((ans) => ans !== option);
        } else {
            newAnswers = questionIndex === 0 ? [...currentAnswers, option] : [option];
        }
        setFormData({
            ...formData,
            answers: { ...formData.answers, [questionIndex]: newAnswers },
        });
        toast(`${option} ${newAnswers.includes(option) ? "selected" : "deselected"}`, {
            duration: 1000,
        });
    };

    const borderColors = [
        "border-red-500",
        "border-blue-500",
        "border-green-500",
        "border-yellow-500",
        "border-purple-500",
    ];

    const isPageComplete = () =>
        currentQuestions.every((q) => formData.answers[q.id - 1]?.length > 0);

    const fileToBase64 = (file) =>
        new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = () => resolve(reader.result.split(",")[1]);
            reader.onerror = (error) => reject(error);
        });

    return (
        <>
            <Header />
            <div className="min-h-screen bg-gray-100 flex flex-col justify-center items-center">
                <div className="max-w-3xl w-full mx-auto p-6">
                    <Toaster position="top-right" reverseOrder={false} />

                    {/* Step 1: Initial Form */}
                    {step === 1 && (
                        <div>
                            <h2 className="text-2xl font-bold text-[#231812] mb-4 text-center">
                                Let’s Get Started
                            </h2>
                            <div className="space-y-4">
                                <input
                                    type="text"
                                    name="fullName"
                                    value={formData.fullName}
                                    onChange={handleChange}
                                    placeholder="Full Name"
                                    className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:border-[#f05d23]"
                                />
                                <input
                                    type="email"
                                    name="email"
                                    value={formData.email}
                                    onChange={handleChange}
                                    placeholder="Email"
                                    className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:border-[#f05d23]"
                                />
                                <input
                                    type="tel"
                                    name="phone"
                                    value={formData.phone}
                                    onChange={handleChange}
                                    placeholder="Phone"
                                    className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:border-[#f05d23]"
                                />
                                <input
                                    type="text"
                                    name="linkedin"
                                    value={formData.linkedin}
                                    onChange={handleChange}
                                    placeholder="LinkedIn Profile (e.g., https://linkedin.com/in/...)"
                                    className="w-full p-2 border border-gray-300 rounded focus:outline-none focus:border-[#f05d23]"
                                />
                            </div>
                        </div>
                    )}

                    {/* Step 2: Questionnaire */}
                    {step === 2 && (
                        <div>
                            <div className="flex justify-between items-center mb-1">
                                <p className="text-xs text-[#231812] font-semibold">
                                    {Math.round(((currentPage + 1) / totalPages) * 100)}%
                                </p>
                                <p className="text-xs text-[#231812] font-semibold">
                                    Page {currentPage + 1} of {totalPages}
                                </p>
                            </div>
                            <div className="w-full bg-gray-200 rounded-full h-2 mb-6">
                                <div
                                    className="bg-[#f05d23] h-2 rounded-full transition-all duration-300"
                                    style={{ width: `${((currentPage + 1) / totalPages) * 100}%` }}
                                ></div>
                            </div>
                            <div className="space-y-6">
                                {currentQuestions.map((question) => (
                                    <div
                                        key={question.id}
                                        className="bg-white shadow-md rounded-lg p-4 transform transition-all hover:scale-105"
                                    >
                                        <p className="text-center text-lg font-bold text-[#231812] mb-5">
                                            {question.text}
                                        </p>
                                        <div className="grid grid-cols-2 md:grid-cols-5 gap-4 place-items-center">
                                            {question.options.map((option, index) => {
                                                const isSelected = (
                                                    formData.answers[question.id - 1] || []
                                                ).includes(option);
                                                return (
                                                    <div
                                                        key={option}
                                                        className="flex flex-col items-center cursor-pointer"
                                                        onClick={() =>
                                                            handleOptionToggle(question.id - 1, option)
                                                        }
                                                    >
                                                        <div
                                                            className={`w-10 h-10 rounded-full flex items-center justify-center border-2 ${
                                                                borderColors[index % borderColors.length]
                                                            } ${
                                                                isSelected
                                                                    ? "bg-[#f05d23] border-[#f05d23]"
                                                                    : "bg-white"
                                                            } transition-all duration-200`}
                                                        >
                                                            {isSelected && (
                                                                <svg
                                                                    viewBox="0 0 24 24"
                                                                    className="w-6 h-6"
                                                                    fill="#ffffff"
                                                                >
                                                                    <path d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z" />
                                                                </svg>
                                                            )}
                                                        </div>
                                                        <p className="text-xs font-semibold text-center mt-2 text-[#231812]">
                                                            {option}
                                                        </p>
                                                    </div>
                                                );
                                            })}
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    )}

                    {/* Step 3: End Form */}
                    {step === 3 && (
                        <div>
                            <h2 className="text-2xl font-bold text-[#231812] mb-4 text-center">
                                Submit Your Documents
                            </h2>
                            <div className="space-y-4">
                                <div>
                                    <label className="block text-[#231812] mb-1">
                                        Resume (PDF or DOCX)
                                    </label>
                                    <input
                                        type="file"
                                        name="resume"
                                        accept=".pdf,.docx"
                                        onChange={handleChange}
                                        className="w-full p-2 border border-gray-300 rounded"
                                    />
                                </div>
                                <div>
                                    <label className="block text-[#231812] mb-1">
                                        Cover Letter (PDF or DOCX)
                                    </label>
                                    <input
                                        type="file"
                                        name="coverLetter"
                                        accept=".pdf,.docx"
                                        onChange={handleChange}
                                        className="w-full p-2 border border-gray-300 rounded"
                                    />
                                </div>
                            </div>
                        </div>
                    )}

                    {/* Step 4: Confirmation */}
                    {step === 4 && (
                        <div className="text-center">
                            <h2 className="text-2xl font-bold text-[#231812] mb-4">
                                Thank You, {formData.fullName}!
                            </h2>
                            <p className="text-lg text-[#231812] mb-4">
                                Your application has been successfully submitted.
                            </p>
                            <p className="text-lg font-semibold text-[#f05d23] mb-6">
                                Your Score: {submissionStatus.score}/190
                            </p>
                            <p className="text-sm text-[#231812]">
                                We’ve sent a confirmation email to {formData.email}. Please
                                check your inbox (and spam folder) for further details.
                            </p>
                        </div>
                    )}

                    {/* Navigation */}
                    {step !== 4 && (
                        <div className="mt-6 flex justify-between">
                            <button
                                onClick={handleBack}
                                disabled={step === 1 || isSubmitting}
                                className="px-4 py-2 bg-gray-400 text-white rounded disabled:opacity-50 hover:bg-gray-500 transition"
                            >
                                Back
                            </button>
                            <button
                                onClick={handleNext}
                                disabled={
                                    (step === 2 && !isPageComplete()) || isSubmitting
                                }
                                className="w-full md:w-auto px-4 py-2 bg-[#f05d23] text-white rounded hover:bg-[#d94f1e] disabled:bg-gray-300 disabled:text-gray-600 transition"
                            >
                                {step === 3 ? "Submit" : "Next"}
                            </button>
                        </div>
                    )}
                </div>
            </div>
        </>
    );
}